* {
    font-family: 'Roboto', sans-serif;

    .searchPokemonContainer {
        background-image: url("https://assets.pokemon.com/static2/_ui/img/chrome/container_bg.png");
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100vw;
        height: 100vh;
        
        .buttonContainer {
            display: flex;
            align-items: center;
            justify-content: center;

            
            button {
                font-size: 7ch;
                font-weight: 700;
                background: none;
                border-radius: 10px;
                border: none;
            }

            button:hover {
                cursor: pointer;
            }
        }

    }
    
}