.pokemonContainer {
  display: flex;
  align-items: center;
  flex-direction: column;
  background-image: url("https://assets.pokemon.com/static2/_ui/img/chrome/body_bg.png");
  color: #FFF;
  border-radius: 10px;
  margin: 20px 0;
}
.pokemonContainer .imageContainer img {
  width: 80vw;
}
.pokemonContainer .dataContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}
.pokemonContainer .dataContainer .specContainer {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: space-around;
}

@media (min-width: 1060px) {
  .pokemonContainer {
    flex-direction: row;
    justify-content: center;
    width: 90vw;
    height: 80vh;
  }
  .pokemonContainer .imageContainer img {
    width: 50vw;
  }
}
@media (min-width: 1300px) {
  .pokemonContainer .imageContainer img {
    width: 40vw;
  }
}
@media (min-width: 1600px) {
  .pokemonContainer .imageContainer img {
    width: 30vw;
  }
}/*# sourceMappingURL=PokemonSpecs.css.map */